export default {
	data() {
		return {
			collect: false,
			subject: '',
			listData: [],
			radioSelect: '5',
			judgeSelect: '2',
			CheckboxSelect: '',
			open: false,
			questionId: 0,
			eq: 0,
			data: [],
			imageShow: false,
			width: '',
			height: '',
			yes: 0,
			wrong: 0,
		}
	},
	onLoad(options) {
		this.subject = options.subject
		this.examData()
	},
	methods: {
		// 获取答题卡
		AnswerCard: function(e) {
			uni.request({
				url: this.$Url + '/api/v1/exam/study/card',
				method: 'GET',
				data: {
					'token': uni.getStorageSync('token'),
					'subject': this.subject,
					'model': uni.getStorageSync('cars_mold')
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if (res.data.code == 200) {
						this.data = res.data.msg.item
						let arr = []
						this.yes = 0
						this.wrong = 0
						for (let i in res.data.msg.item) {
							if (this.questionId == res.data.msg.item[i].questionId) {
								this.eq = Number(i) + 1
							}
							if (res.data.msg.item[i].sign == 'r') {
								this.yes++
							} else if (res.data.msg.item[i].sign == 'w') {
								this.wrong++
							}
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络不给力，请稍后重试',
							duration: 2000
						});
					}
				}
			});
		},
		// 获取题目
		examData: function(e) {
			uni.request({
				url: this.$Url + '/api/v1/exam/study/list',
				method: 'GET',
				data: {
					'token': uni.getStorageSync('token'),
					'subject': this.subject,
					'model': uni.getStorageSync('cars_mold')
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					// 考题
					uni.showToast({
						icon: 'loading',
						title: '题目加载中...'
					});
					uni.hideToast()
					if (res.data.code == 200) {
						var arr = []
						for (let i in res.data.msg) {
							arr.push(res.data.msg[i]);
						}
						console.log(arr);
						this.listData = arr
						this.questionId = arr[0].questionId
						if (arr[1] != null) {
							this.collect = true
						} else {
							this.collect = false
						}
						console.log(this.collect);

					} else {
						uni.showToast({
							icon: 'none',
							title: '网络不给力，请稍后重试',
							duration: 2000
						});
					}
				}
			});
			this.AnswerCard()
		},
		//答题
		DoTitle: function(sign, choose) {
			uni.request({
				url: this.$Url + '/api/v1/exam/item/log',
				method: 'GET',
				data: {
					token: uni.getStorageSync('token'),
					subject: this.subject,
					questionId: this.questionId,
					sign: sign,
					answer: this.listData[0].answer,
					choose: choose
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {}
			});
		},
		tapChange: function(str) {
			uni.request({
				url: this.$Url + '/api/v1/exam/study/change',
				method: 'GET',
				data: {
					'token': uni.getStorageSync('token'),
					'questionId': this.questionId,
					// 'subject': this.subject,
					'type': str,
					'model': uni.getStorageSync('cars_mold')
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					// 考题
					uni.showToast({
						icon: 'loading',
						title: '题目加载中...'
					});
					uni.hideToast()
					if (res.data.code == 200) {
						var arr = []
						for (let i in res.data.msg) {
							arr.push(res.data.msg[i]);
						}
						if (arr.length > 0) {
							console.log(arr)
							this.listData = arr
							this.questionId = arr[0].questionId
							if (arr[1] != null) {
								this.collect = true
							} else {
								this.collect = false
							}
							// 获取答题卡
							this.AnswerCard()
							if (arr[0].item_log != null) {
								console.log(arr[0].item_log.choose)
								if (arr[0].item3 != '' && arr[0].answer < 6) {
									this.radioSelect = Number(arr[0].item_log.choose) - 1
								} else if (item.item3 == '') {
									this.judgeSelect = Number(arr[0].item_log.choose) - 1
								}
							} else {
								this.radioSelect = '5'
								this.judgeSelect = '2'
							}
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络不给力，请稍后重试',
							duration: 2000
						});
					}
				}
			});

		},
		tapCollect: function(e) {
			this.collect = !this.collect
			if (this.collect) {
				uni.request({
					url: this.$Url + '/api/v1/exam/collection/store',
					method: 'POST',
					data: {
						'token': uni.getStorageSync('token'),
						'questionId': this.questionId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.data.code == 200) {
							uni.showToast({
								title: '收藏成功',
								duration: 1000
							});
						} else {
							uni.showToast({
								icon: 'none',
								title: '网络不给力，请稍后重试',
								duration: 2000
							});
						}
					}
				});
			} else {
				uni.request({
					url: this.$Url + '/api/v1/exam/collection/cancel',
					method: 'POST',
					data: {
						'token': uni.getStorageSync('token'),
						'questionId': this.questionId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.data.code == 200) {
							uni.showToast({
								title: '取消成功',
								duration: 1000
							});
						} else {
							uni.showToast({
								icon: 'none',
								title: '网络不给力，请稍后重试',
								duration: 2000
							});
						}
					}
				});
			}

		},
		//单选题
		radioChange: function(e) {
			this.radioSelect = e.target.value
			if (this.listData[0].answer - 1 == e.target.value) {
				this.DoTitle('r', this.listData[0].answer)
				uni.showModal({
					title: '温馨提示',
					content: '恭喜您答对了',
					cancelText: '解析',
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							console.log('用户点击确定');
							this.radioSelect = 5
							this.examData()
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else {
				this.DoTitle('w', parseInt(e.target.value) + 1)
				uni.showModal({
					title: '温馨提示',
					content: '很抱歉您答错了',
					cancelText: '解析',
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							console.log('用户点击确定');
							this.radioSelect = 5
							this.examData()
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
		},
		//多选题
		checkboxChange: function(e) {
			let str = ''
			switch (e.detail.value.join('')) {
				case 'AB':
					str = "7";
					break;
				case 'AC':
					str = "8";
					break;
				case 'AD':
					str = "9";
					break;
				case 'BC':
					str = "10";
					break;
				case 'BD':
					str = "11";
					break;
				case 'CD':
					str = "12";
					break;
				case 'ABC':
					str = "13";
					break;
				case 'ABD':
					str = "14";
					break;
				case 'ACD':
					str = "15";
					break;
				case 'BCD':
					str = "16";
					break;
				case 'ABCD':
					str = "17";
					break;
			}
			this.CheckboxSelect = str
		},
		//判断题
		judgeChange: function(e) {
			this.judgeSelect = e.target.value
			console.log(this.listData[0].answer)
			if (this.listData[0].answer - 1 == e.target.value) {
				this.DoTitle('r', this.listData[0].answer)
				uni.showModal({
					title: '温馨提示',
					content: '恭喜您答对了',
					cancelText: '解析',
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							console.log('用户点击确定');
							this.judgeSelect = 2
							this.examData()
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else {
				this.DoTitle('w', parseInt(e.target.value) + 1)
				uni.showModal({
					title: '温馨提示',
					content: '很抱歉您答错了',
					cancelText: '解析',
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							console.log('用户点击确定');
							this.judgeSelect = 2
							this.examData()
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
		},
		//多选题提交答案
		tapCheckbox: function(e) {
			if (this.listData[0].answer == this.CheckboxSelect) {
				this.DoTitle('r', this.CheckboxSelect)
				uni.showModal({
					title: '温馨提示',
					content: '恭喜您答对了',
					cancelText: '解析',
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							console.log('用户点击确定');
							this.CheckboxSelect = ''
							this.examData()
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else {
				this.DoTitle('w', this.CheckboxSelect)
				uni.showModal({
					title: '温馨提示',
					content: '很抱歉您答错了',
					cancelText: '解析',
					confirmText: '下一题',
					success: (res) => {
						if (res.confirm) {
							console.log('用户点击确定');
							this.CheckboxSelect = ''
							this.examData()
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
		},

		tapPrev: function(e) {
			this.tapChange('back')
		},
		tapNext: function(e) {
			this.tapChange('next')
		},
		tapAnswer: function(e) {
			this.open = !this.open
			this.AnswerCard()
		},
		CloseAnswer: function(e) {
			this.open = !this.open
		},
		tapSelTopic: function(e) {
			this.open = !this.open
			this.questionId = e.currentTarget.dataset.questionid
			uni.request({
				url: this.$Url + '/api/v1/exam/study/choose',
				method: 'GET',
				data: {
					'token': uni.getStorageSync('token'),
					'subject': this.subject,
					'questionId': this.questionId
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					uni.hideToast();
					if (res.data.code == 200) {
						console.log(res.data.msg)
						var arr = []
						for (let i in res.data.msg) {
							arr.push(res.data.msg[i]);
						}
						console.log(arr);
						this.listData = arr
						this.questionId = arr[0].questionId
						if (arr[1] != null) {
							this.collect = true
						} else {
							this.collect = false
						}
						if (arr[0].item_log != null) {
							console.log(arr[0].item_log.choose)
							if (arr[0].item3 != '' && arr[0].answer < 6) {
								this.radioSelect = Number(arr[0].item_log.choose) - 1
							} else if (item.item3 == '') {
								this.judgeSelect = Number(arr[0].item_log.choose) - 1
							}
						} else {
							this.radioSelect = '5'
							this.judgeSelect = '2'
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络不给力，请稍后重试',
							duration: 2000
						});
					}
				}
			});
			// 获取答题卡
			this.AnswerCard()
		},
		imageLoad: function(e) {
			console.log('image发生load事件，携带值为' + e.detail.height + e.detail.width)
			this.height = e.detail.height
			this.width = e.detail.width
			this.imageShow = true
		}
	}
}
